import datePicker from "./date-picker.vue"
import rangePicker from "./range-picker.vue"
import halfPicker from "./half-picker.vue"
import shorttermPicker from "./shortterm-picker.vue"
import timePicker from "./time-picker.vue"
import selectorPicker from "./selector-picker.vue"
import regionPicker from "./region-picker.vue"
import linkagePicker from "./linkage-picker.vue"
export default {
	name:"w-picker",
	components:{
		datePicker,
		rangePicker,
		halfPicker,
		timePicker,
		selectorPicker,
		shorttermPicker,
		regionPicker,
		linkagePicker
	},
	props:{
		mode:{
			type:String,
			default:"date"
		},
		value:{//默认值
			type:[String,Array,Number],
			default:""
		},
		current:{//是否默认显示当前时间，如果是，传的默认值将失效
			type:Boolean,
			default:false
		},
		themeColor:{//确认按钮主题颜色
			type:String,
			default:"#f5a200"
		},
		fields:{//日期颗粒度:year、month、day、hour、minute、second
			type:String,
			default:"date"
		},
		disabledAfter:{//是否禁用当前之后的日期
			type:Boolean,
			default:false
		},
		second:{//time-picker是否显示秒
			type:Boolean,
			default:true
		},
		options:{//selector,region数据源
			type:[Array,Object],
			default(){
				return []
			}
		},
		defaultProps:{//selector,linkagle字段转换配置
			type:Object,
			default(){
				return{
					label:"label",
					value:"value",
					children:"children"
				}
			}
		},
		defaultType:{
			type:String,
			default:"label"
		},
		hideArea:{//mode=region时，是否隐藏区县列
			type:Boolean,
			default:false
		},
		level:{
			//多级联动层级，表示几级联动,区间2-4;
			type:[Number,String],
			default:2
		},
		timeout:{//是否开启点击延迟,当快速滚动 还没有滚动完毕点击关闭时得到的值是不准确的
			type:Boolean,
			default:false
		},
		expand:{//mode=shortterm 默认往后拓展天数
			type:[Number,String],
			default:30
		},
		startYear:{
			type:[String,Number],
			default:1970
		},
		endYear:{
			type:[String,Number],
			default:new Date().getFullYear()
		}
	},
	created() {
		this.createKey=Math.random()*1000;
	},
	data() {
		return {
			itemHeight:`height: ${uni.upx2px(88)}px;`,
			visible:false,
			result:{},
			confirmFlag:true
		};
	},
	methods:{
		touchStart(){
			if(this.timeout){
				this.confirmFlag=false;
			}
		},
		touchEnd(){
			if(this.timeout){
				setTimeout(()=>{
					this.confirmFlag=true;
				},500)
			}
		},
		handlerChange(res){
			let _this=this;
			this.result={...res};
		},
		show(){
			this.visible=true;
		},
		hide(){
			this.visible=false;
		},
		onCancel(res){
			this.visible=false;
			this.$emit("cancel");
		},
		pickerConfirm(){
			if(!this.confirmFlag){
				return;
			};
			this.$emit("confirm",this.result);
			this.visible=false;
		}
	}
}
